.statistics {
  width: 250px;
  height: 150px;
  margin-top: 30px;
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.1);
}
.statistics h2 {
  display: flex;
  justify-content: center;
}
.statlist {
  list-style: none;
  display: flex;
  padding: 0;
  margin-bottom: 0;
  justify-content: space-around;
  width: 100%;
  height: 52%;
}
.item {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;

  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
}
.percentage {
  display: flex;
  justify-content: center;
  color: white;
}
.label {
  display: flex;
  justify-content: center;
  color: white;
}
